import axios from 'axios';//axios

export function fetch(options){
    return new Promise((resolve, reject) => {
      const instance = axios.create({  //instanceaxios axios
        //
        headers: {
          'Content-Type': 'application/json'
          // 'token_in_header': global_.token,//token
        },
        timeout:30 * 1000 // 30
      });
      instance(options)
        .then(response => { //then 

          resolve(response);//
        })
        .catch(error => {
          console.log(''+error);
          reject(error);
        });
    });
}


