            margin-left: 20px;
            min-height: 450px;
            margin-right: 20px;
            text-align: left;

            .anchor_description {
                padding-top: 0px;
            }

            .anchor_ability {
                padding-top: 40px;
                font-size: 14pt;
                padding-bottom: 20px;
            }

            .anchor_detail {
                margin-bottom: 40px;
            }

            .anchor_return_top {
                height: 15px;
                line-height: 15px;
                text-align: right;
                margin-bottom: 25px;
                a {
                    text-decoration-line: none;
                    color: black;

                    :hover {
                        color: #929497;
                    }
                }
            }

        }
    }


    .anchor_background {
        background-color: #fefefe;
    }
</style>





